<template>
    <div class="black-bg" v-if="modalopen == true"> 
        <div class="white-bg">
            <div><img :src="datarooms[modaltarget].image"></div>
            <h3>{{datarooms[modaltarget].title}}</h3>
            <p>{{datarooms[modaltarget].content}}</p>
            <p><input type="range" min="1" max="12" v-model="month"></p>
            <input type="text" class="month" v-model.number="month">  
            <!-- <input type="text" @input="month = $event.target.value"> -->
            <p class="price">{{month}}  : {{datarooms[modaltarget].price * month}}  </p>
            <!-- <p class="price">{{datarooms[modaltarget].price}}</p> -->
            <button @click="$emit('closeModal')"></button>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Modal',
    data(){
      return{
        month: 1,
      }
    },
    watch:{// 
      month(a){//  (a,b)(,)
        if(isNaN(a) == true){
          alert('   ');
          this.month='1';
        } else if(a > 13){
          alert('13  ');
          this.month='1';
        }
      },
    },
    props: {
      datarooms : Array,
      modalopen: Boolean,
      modaltarget: Number,
      //: (String)
      //: Object
    },
    updated() {//lifecycle hook
      if(this.month == 2){
        alert('!!');
        this.month = 12;//2  12
      }       
    },
}
</script>

